import axios from 'axios'

function About(props) {
  return (
    <div>
      <div>About</div>
      <div> {props.value} </div>
    </div>
  );
}

About.getInitialProps = async () => {
  const res = await axios.get('https://binance-api-example.herokuapp.com/')
  const data = res.data
  const list = data.filter(val => val.slug === 'about')
  console.log(list[0].content)
  return {content: list[0].content} 
};

export default About;
